<template>
  <div>
    <ld-table
      :tableData="config.tableData" :columns="config.fieldConfig" @clickItem="onClickItem"
      border
      :editMode="config.canEdit"
      @onInput="onInput"
    ></ld-table>
  </div>
</template>
<script>
import ld from '../utils/ld';
import Table from '../components/Table';
import { CHILD_DATA_INDEX_KEY } from '../utils/const';

export default {
  components: {
    'ld-table': Table,
  },
  props: ['config', 'onClick'],
  methods: {

    /**
     * @param {Object} item 
     * @param {number} colIdx 0
     */

    onClickItem(item, rowIdx) {
      if (this.onClick) {
        this.onClick(item, rowIdx);
        return;
      }
      const row = this.config.fieldConfig[rowIdx];
      if (row.inputType == 1) {
        ld.navigateTo(this.config, item, rowIdx);
        // 
        ld.globalData.set(CHILD_DATA_INDEX_KEY, rowIdx);
      }
    },

    /**
     * 
     */

    onInput(...args) {
      this.$emit('onInput', ...args);
    }
  }
};
</script>

<style scoped>
</style>



