<template>
  <div class="app-container">
    <div>
      <ld-header></ld-header>
    </div>
    <div class="view-container">
      <ld-input
        class="input-item"
        v-for="(item, index) in config.fieldConfig"
        :key="index"
        :label="item.fieldTitle"
        v-model="config.tableData[item.fieldName]"
        @click="onClick(item)"
      ></ld-input>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
import Input from '../../components/Input';
import { INPUT_TYPE_TEXT, INPUT_TYPE_URL } from '../../utils/const';
import ld from '../../utils/ld';
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import service from '../../service';

export default {
  data() {
    return {
      config: {
        fieldConfig: [
          {
            fieldName: 'subjectName',
            fieldTitle: '',
            inputType: INPUT_TYPE_URL, // 
            url: '/E12/index2',
          },
          {
            fieldName: 'jine',
            fieldTitle: '',
            inputType: INPUT_TYPE_URL, // 
            url: '/E12/index4',
          },
          {
            fieldName: 'shuie',
            fieldTitle: '',
            inputType: INPUT_TYPE_TEXT, // 
          },
          {
            fieldName: 'riqi',
            fieldTitle: '',
            inputType: INPUT_TYPE_TEXT, // 
          },
          {
            fieldName: 'beizhu',
            fieldTitle: '',
            inputType: INPUT_TYPE_TEXT, // 
          },
        ],
        tableData: {
          subjectName: '',
          jine: '',
          shuie: '',
          riqi: '',
          beizhu: '',
        },
      },
    };
  },
  components: {
    'ld-input': Input,
    'ld-header': Header,
    'ld-bottom': Bottom,
  },
  mounted() {
    this.loadData();
  },
  methods: {
    async loadData() {
      try {
        let urlParams; // url
        let subjectName; // 
        let subjectNo; // 
        let viewConfig; // 
        let storageKey; // 

        urlParams = ld.getUrlParams();
        subjectName = urlParams.subjectName;
        subjectNo = urlParams.subjectNo;

        // 
        this.config.tableData['subjectName'] = subjectName;
        this.config.tableData['subjectNo'] = subjectNo;

        // 
        viewConfig = await service.getMockViewConfig('/E12/index3');

        Object.assign(this.config, viewConfig); // config

        // storage key
        // 
        this.storageKey = `${viewConfig.jianzhiming}`;

        this.handleChildData();
      } catch (err) {
        console.log(err);
      }
    },
    handleChildData() {
      const childData = ld.globalData.get(this.storageKey); // storageKey
      if (!childData) {
        return;
      }

      this.config.tableData.jine = ld.sumWithKey(childData, 'jine');
    },

    /**
     * 
     */

    onClick(item) {
      let inputType; // 
      let url; // url
      let urlParams; // url

      inputType = item.inputType;
      url = item.url;
      urlParams = ld.getUrlParams();

      if (inputType === INPUT_TYPE_URL) {
        urlParams['storageKey'] = this.storageKey;

        // 
        ld.navigateTo(url, urlParams);
      }
    }
  }
}
</script>
<style scoped>
.input-item {
  margin-bottom: 2.4rem;
}
</style>


