/**
 * @file ajax
 */

/**
 * get
 * @param {Object} obj 
 * @return {string}
 */
function resolveParams(obj) {
  return (new URLSearchParams(obj)).toString();
}

/**
 * 
 * @param {Object} param0 
 * @param {string} param0.url 
 * @param {string} param0.method 
 * @param {Object} param0.data 
 * @return {Promise} 
 */
function request({ url, method, data } = {}) {
  const query = resolveParams(data);
  url = `${url}?${query}`;
  const xhr = new XMLHttpRequest();

  return new Promise((resolve, reject) => {
    xhr.onload = () => {
      if (!xhr.readyState === 4) {
        return;
      }

      if (xhr.status >= 200 && xhr.status <= 300) {
        resolve(xhr.responseText);
      } else {
        reject({
          status: xhr.status,
          statusText: xhr.statusText,
        });
      }
    };

    xhr.onerror = () => {
      reject({
        status: xhr.status,
        statusText: xhr.statusText
      });
    };

    xhr.open(method.toUpperCase() || 'GET', url);
    xhr.send();
  });
}

/**
 *
 * @param {string} url 
 * @param {Object} data
 */
export const get = (url, data) => {
  return request({
    url,
    data,
    method: 'GET',
  });
};

export const post = (url, data) => {
  return request({
    url,
    data,
    method: 'POST',
  });
};



