<template>
  <div class="ld-dialog" v-show="show" @touchend="onClose">
    <div class="dialog-content" @touchend.stop="onStop">
      <div class="dia-text ld-border bottom">{{ text }}</div>
      <div class="dia-options">
        <div
          class="confirm item"
          :class="{ highlight: confirmHighlight }"
          @touchend="onAction('confirm')"
        >
          
        </div>
        <div
          class="close item ld-border left"
          :class="{ highlight: cancelHighlight }"
          @touchend="onAction('cancel')"
        >
          
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    show: Boolean,
    text: String,
    cancelHighlight: false,
    confirmHighlight: false,
  },
  model: {
    prop: 'show',
    event: 'ld-show-dialog',
  },
  methods: {
    onAction(action) {
      this.$emit(action);
      this.onClose(action);
    },
    onClose(action) {
      this.$emit('ld-show-dialog', false);
      this.close();

      if (this.callback) {
        this.callback(action);
      }
    },
    // 
    onStop() {},
  }
};
</script>
<style lang="scss">
.ld-dialog {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  top: 0; left: 0;
  padding: 0 4.8rem;
}
.dialog-content {
  width: 100%;
  background: #fff;
  border-radius: 4px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.dia-text {
  width: 100%;
  padding: 3.2rem 1.6rem;
  text-align: center;
}
.dia-options {
  display: flex;
  .item {
    flex: 1;
    line-height: 4.5rem;
    text-align: center;
  }
  .item.highlight {
    color: #0AF;
  }
}
</style>



