<template>
  <div v-if="config" class="container">
    <div v-if="config.viewName">
      <ld-header>{{ config.viewName }}</ld-header>
    </div>
    <div v-if="config.viewType" class="view-container">
      <component :is="`view-${config.viewType.toLowerCase()}`" :config="config"></component>
    </div>
    <div v-if="backEndData">
      <ld-table :columns="backEndColumns" :tableData="backEndData"></ld-table>
    </div>
    <div>
      <ld-bottom :list="bottom"></ld-bottom>
    </div>
  </div>
</template>
<script>
/**
 * @file 
 */

import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import Table from '../../components/Table';
import ViewA from '../../view/A';
import ViewB from '../../view/B';
import service from '../../service/index.js';
import ld from '../../utils/ld';
import { CHILD_DATA_VALUE_KEY, CHILD_DATA_INDEX_KEY, KEY_ID, BACK_END_KEY, BACK_END_COL_KEY } from '../../utils/const';

export default {
  data() {
    return {
      config: null,
      bottom: [
        {},
        {
          icon: 'confirm',
          onClick: () => {
            const globKey = this.config.globKey;
            const tableData = this.config.tableData;

            ld.globalData.set(globKey, tableData);
            ld.globalData.set(CHILD_DATA_VALUE_KEY, tableData);
            ld.globalData.set(ld.getUrlParams()[KEY_ID], tableData);

            //  ========= start

            const allGlobData = ld.globalData.getAll(); // 
            const allChildData = []; // 
            const jianzhiming = ld.getUrlParams()[KEY_ID].split('_')[0]; // 

            Object.keys(allGlobData).forEach(key => {

              if (~key.indexOf(jianzhiming)) {
                const childDataItem = {};
                const storageItem = allGlobData[key];

                const keyIdx = key.split('_')[1];

                // 
                this.config.fieldConfig.forEach(conf => {
                  const fieldName = conf.fieldName;
                  // 
                  childDataItem[fieldName] = ld.sumWithKey(storageItem, fieldName);
                });
                childDataItem[KEY_ID] = key;

                // 
                const curViewItem = tableData[keyIdx];

                allChildData[keyIdx] = childDataItem;
              }
            });

            ld.globalData.set(BACK_END_KEY, allChildData);
            ld.globalData.set(BACK_END_COL_KEY, [{fieldName: KEY_ID, title: KEY_ID}, ...this.config.fieldConfig]);

            //  ========= end

            ld.navigateBack();
          }
        }
      ],
      backEndData: null,
      backEndColumns: null,
    }
  },
  mounted() {
    this.loadData();
  },
  watch: {
    async '$route'() {
      this.config = null;
      this.loadData();
    }
  },
  methods: {
    /**
     * 
     */

    loadData() {
      const urlParams = ld.getUrlParams(); // url
      const { viewId } = urlParams; // id
      const config = service.getMockData(viewId);
      this.config = config;

      // 
      const addData = service.getMockAddData(viewId);
      if (addData) {
        this.config.tableData = addData;
      }

      if (config.queryList) {
        this.handleUrlParams();
      }

      this.setGlobalKey(); // 

      const globData = ld.globalData.get(this.config.globKey);
      // 
      if (globData) {
        this.config.tableData = globData; // 
      }

      // 
      this.handleChilrenData();
    },

    handleChilrenData() {
      const childrenData = ld.globalData.get(CHILD_DATA_VALUE_KEY);
      if (!childrenData) {
        return;
      }

      const rowIdx = ld.globalData.get(CHILD_DATA_INDEX_KEY);
      this.config.tableData[rowIdx]['zhangshu'] = ld.sumWithKey(childrenData, 'zhangshu');
      this.config.tableData[rowIdx]['jine'] = ld.sumWithKey(childrenData, 'jine');

      // 

      const backEndData = ld.globalData.get(BACK_END_KEY);
      const backEndColumns = ld.globalData.get(BACK_END_COL_KEY);
      // 
      backEndData.forEach((item, index) => {
        item['piaoju'] = this.config.tableData[index]['piaoju'];
      });
      this.backEndData = backEndData;

      backEndColumns.unshift({ fieldName: 'piaoju', fieldTitle: '' });
      this.backEndColumns = backEndColumns;

      // const allGlobData = ld.globalData.getAll(); // 
      // const allChildData = []; // 

      // Object.keys(allGlobData).forEach(key => {

      //   if (~key.indexOf(this.config.jianzhiming)) {
      //     const childDataItem = {};
      //     const storageItem = allGlobData[key];

      //     const keyIdx = key.split('_')[1];

      //     // 
      //     this.config.fieldConfig.forEach(conf => {
      //       const fieldName = conf.fieldName;
      //       // 
      //       if (~this.config.sumFields.indexOf(conf.fieldName)) {
      //         // 
      //         childDataItem[fieldName] = ld.sumWithKey(storageItem, fieldName);
      //         return;
      //       }

      //       // 
      //       childDataItem[fieldName] = this.config.tableData[keyIdx][fieldName];
      //     });
      //     childDataItem[KEY_ID] = key;

      //     allChildData.push(childDataItem);
      //   }
      // });

      // ld.globalData.set('A', allChildData);

      // // 

      // // 
      // this.backEndData = allChildData;
      // // 
      // this.backEndColumns = [{ fieldName: KEY_ID, title: KEY_ID }, ...this.config.fieldConfig];
    },

    /**
     * url
     */

    handleUrlParams() {
      const { queryList } = this.config;
      const urlParams = ld.getUrlParams(); // url

      if (queryList && queryList.length) {
        if (!this.config.state) {
          this.config.state = {};
        }

        queryList.forEach(query => {
          this.config.state[query] = urlParams[query]; // url
        });
      }
      console.log(this.config.state);
    },

    /**
     * 
     * 
     * viewId_view1__...
     */

    setGlobalKey() {
      const state = this.config.state;
      const queryList = this.config.queryList;

      // key
      let globKeyArr = [];

      if (queryList) {
        const arr = queryList.map(query => {
          return [query, state[query]].join('_');
        });
        globKeyArr.push(arr);
      }

      globKeyArr.unshift(['viewId'], this.config.viewId); // 

      this.config.globKey = globKeyArr.join('_');
    }
  },
  components: {
    'view-a': ViewA,
    'view-b': ViewB,
    'ld-header': Header,
    'ld-bottom': Bottom,
    'ld-table': Table,
  }
}
</script>
<style scoped>
.container {
  display: flex;
  min-height: 100vh;
  height: 100vh;
  flex-direction: column;
}
.view-container {
  flex: 1;
  overflow: auto;
}
</style>


