<template>
  <footer class="ld-tabbar">
    <div
      class="ld-tabbar-item"
      :class="{ [item.icon]: true }"
      v-for="(item, index) in selfList"
      :key="'tabbar' + index"
      @touchend="onClick(index)"
    >
      <span
        v-if="!item.icon && item.text"
        class="ld-tabbar-text"
      >{{ item.text }}</span>
    </div>
  </footer>
</template>
<script>
import ld from '../utils/ld';

export default {
  props: {
    /**
     * [{ icon: , text: , onClick:  }, ...]
     * onClickthisthis
     * icon
     * 3X
     */
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  computed: {
    selfList() {
      const res = this.list.slice();
      while (res.length < 3) {
        if (res.length === 2) {
          res.push({
            icon: 'close',
            onClick() {
              ld.navigateBack();
            },
          });
        } else {
          res.push({});
        }
      }
      return res;
    },
  },
  methods: {
    onClick(idx) {
      const fn = this.selfList[idx] && this.selfList[idx].onClick;
      fn && fn.call(this.$parent);
    },
  },
};
</script>
<style lang="scss">
.ld-tabbar {
  width: 100%;
  height: 4.5rem;
  background: rgb(250, 250, 250);
  font-size: 1.4rem;
  // 
  box-shadow: 0 -0.3px #ccc;

  display: flex;
}
.ld-tabbar-item {
  width: 33.3%;
  background: 50% 50% no-repeat;
  background-size: 1.6rem 1.6rem;
  text-align: center;
  line-height: 4.5rem;

  &.confirm {
    background-image: url('../assets/img/confirm.png');
  }
  &.close {
    background-image: url('../assets/img/close.png');
  }
  &.delete {
    background-image: url('../assets/img/delete.png');
  }
  &.add {
    background-image: url('../assets/img/add.png');
  }
  &.query {
    background-image: url('../assets/img/query.png');
  }
  &.nav {
    background-image: url('../assets/img/nav.png');
  }
}
.ld-tabbar-text {
  border: 1px solid #333;
  padding: 0.3rem 1.3rem;
  border-radius: 10rem;
}
</style>



