
/**
 * rem1remiphone610px
 */
export default function calculateWith() {
  const doc = document;
  const docEl = doc.documentElement;
  // font-size
  const clientW = window.innerWidth
    || docEl.clientWidth
    || doc.body.clientWidth;

  const fontSize = (10 * clientW) / 375;
  docEl.style.fontSize = `${fontSize}px`;
}



