<template>
  <div class="ld-input">
    <div class="ld-input-title">
      <label for="">{{ label }}:</label>
    </div>
    <div class="ld-input-value ld-border bottom">
      <input type="text" :value="value" @input="onInput" @click="onClick">
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      selfValue: '',
    };
  },
  props: {
    label: String,
    disabled: Boolean,
    value: String | Number,
  },
  model: {
    prop: 'value',
    event: 'ld-input',
  },
  methods: {
    onInput($e) {
      this.$emit('ld-input', $e.target.value);
    },
    onClick($e) {
      this.$emit('click');
    }
  }
};
</script>
<style>
.ld-input {
  font-size: 1.6rem;
  display: flex;
  align-items: center;
  width: 100%;
}
.ld-input-title {
  max-width: 4em;
  flex: 1;
}
.ld-input-value {
  flex: 1;
  position: relative;
}
.ld-input-value input {
  width: 100%;
}
</style>



