<template>
  <div class="app-container">
    <div>
      <ld-header></ld-header>
    </div>
    <div class="view-container">
      <view-a
        v-if="config"
        :config="config"
        @onInput="onInput"
      ></view-a>
    </div>
    <div>
      <ld-bottom :list="bottom"></ld-bottom>
    </div>
  </div>
</template>

<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';
import ld from '../../utils/ld.js';
import service from '../../service';

export default {
  data() {
    const that = this;

    return {
      config: null,
      bottom: [
        {},
        {
          icon: 'confirm',
          onClick() {
            ld.globalData.set(that.storageKey, that.config.tableData);
            ld.navigateBack();
          }
        }
      ]
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    /**
     * 
     */

    async loadData() {
      try {
        let urlParams;
        let fieldConfig; // 
        let viewConfig; // 
        let storageKey; // storage
        let storageData; // storage
        let tableData = [{}];

        // url
        urlParams = ld.getUrlParams();
        // url
        storageKey = urlParams.storageKey;

        // 
        storageData = ld.globalData.get(storageKey);
        if (storageData) {
          tableData = storageData;
        }

        // 
        fieldConfig = await service.getMockFieldConfig('/E12/index5');

        // 
        viewConfig = await service.getMockViewConfig('/E12/index5');

        // 
        this.config = {
          tableData: tableData,
          fieldConfig: fieldConfig, // 
          ...viewConfig,
        };
        // storage
        this.storageKey = storageKey;

      } catch (err) {
        console.log(err);
      }
    },

    /**
     * 
     */

    onInput(item, rowIdx, colIdx) {
      let sumField; // 

      // TODO 
      let zhangshu; // 
      let danjia; // 

      sumField = this.config.sumField;
      zhangshu = Number(item.zhangshu) || 0; // 0
      danjia = Number(item.danjia) || 0;

      // 
      this.config.tableData[rowIdx][sumField] = zhangshu * danjia;
    }
  },
  components: {
    'ld-header': Header,
    'ld-bottom': Bottom,
    'view-a': ViewA,
  },
};
</script>

<style scoped>
.view-container {
  padding-left: 0;
  padding-right: 0;
}
</style>



