<template>
  <div class="app-container">
    <div>
      <ld-header>{{ config.viewName }}</ld-header>
    </div>
    <div class="view-container">
      <div
        class="item ld-textoverflow"
        v-for="(item, index) in config.tableData"
        :key="index"
        @touchend="onClick(item)">
        {{ item.ChargeSubjectName }}
      </div>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
/**
 * @file 
 */

import service from '../../service/index.js';
import { GET_SUBJECT } from '../../http/url';
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ld from '../../utils/ld';

export default {
  data() {
    return {
      config: {
        tableData: [],
        viewName: '',
      },
    };
  },
  components: {
    'ld-header': Header,
    'ld-bottom': Bottom,
  },
  mounted() {
    this.loadData();
  },
  methods: {
    async loadData() {
      try {
        const res = await service.getDecodeData(GET_SUBJECT);
        this.config.tableData = res.table;
        console.log(res);
      } catch (err) {
        console.log(err);
      }
    },
    onClick(item) {
      ld.navigateTo('/E12/index3', {
        subjectName: item.ChargeSubjectName,
        subjectNo: item.ChargeSubjectNo,
      });
    }
  },
}
</script>
<style scoped>
.item {
  width: 11.4rem;
  height: 5rem;
  margin-bottom: 1rem;
  line-height: 5rem;
  text-align: center;
  border-radius: 5px;

  background: rgb(224, 233, 240);
  border: 1px solid rgb(210, 210, 210);

  padding: 0 0.5rem;
}

.view-container {
  display: flex;
}
</style>



