<template>
  <div>
    <table class="ld-table">
      <tr class="th row">
        <th v-if="editMode" style="width:5rem;">
        </th>
        <th
          class="row-item"
          :class="{ 'td-border': showBorder }"
          v-for="col in columns"
          :key="col[0]"
        >
          {{ col.fieldTitle || col.fieldName }}
        </th>
      </tr>
      <tr
        class="tr row"
        v-for="(item, rowIdx) in tableData"
        :key="rowIdx"
      >
        <td v-if="editMode" class="row-order" @touchend="onDelLine">
          {{ rowIdx + 1 }}
        </td>
        <td
          class="td row-item"
          :class="{ 'td-border': showBorder }"
          v-for="(col, colIdx) in columns"
          :key="'td' + col.fieldName"
          @touchend="onClickItem(item, rowIdx, colIdx)"
        >
          <template v-if="col.inputType == 1">
            <!--  -->
            {{ item[col.fieldName] }}
          </template>
          <template v-else-if="col.inputType == 2">
            <!--  -->
            <input type="text" v-model="item[col.fieldName]">
          </template>
          <template v-else-if="col.inputType == 3">
            <!--  -->
            <input type="number" v-model="item[col.fieldName]">
          </template>
          <template v-else-if="col.inputType == 4">
            <!--  -->
            <input type="number" v-model="item[col.fieldName]">
          </template>
          <template v-else-if="col.inputType == 5">
            <!--  -->
          </template>
          <template v-else-if="col.inputType == 6">
            <!--  -->
          </template>
          <template v-else>
            <!--  -->
            {{ item[col.fieldName] }}
          </template>
        </td>
      </tr>
    </table>
    <div v-if="editMode" class="icon-plus" @touchend="onAdd"></div>
  </div>
</template>
<script>
import { clone } from '@/utils';
import '@/components/Dialog';
import ld from '../utils/ld';

/**
 * 1. 
 * 2. 
 * 3. 
 */

export default {
  data() {
    return {
      modal: {
        show: false,
        text: '',
      },
      editConf: {
        row: -1,
        col: -1,
      },
    };
  },
  props: {
    tableData: {
      type: Array,
      default() {
        return [{}];
      },
    },
    /**
     * [{ fieldName: , title: , editable:  }, ...]
     */
    columns: {
      type: Array,
      default() {
        return [];
      },
    },
    border: Boolean, // 
    editMode: Boolean, // 
  },
  computed: {
    showBorder() {
      return this.border || this.editMode;
    },
    // 
    newRowVal() {
      return this.newLine;
    },
    newLine() {
      const obj = {};
      this.columns.forEach(col => {
        obj[col.fieldName] = '';
      });
      return obj;
    }
  },
  watch: {
    tableData: {
      // handle
      handler(table) {
        const { row, col, item } = this.editConf;
        if (this.editMode && (row > -1 && col > -1)) {
          // 
          this.$emit('onInput', item, row, col, table);
        }
      },
      deep: true, // 
    }
  },
  methods: {
    // 
    onClickItem(item, row, col) {
      this.$emit('clickItem', item, row, col);
      this.editConf.row = row;
      this.editConf.col = col;
      this.editConf.item = item;
    },
    onAdd() {
      this.tableData.push(clone(this.newRowVal));
    },
    async onDelLine() {
      try {
        if (this.tableData.length < 1) {
          return;
        }
        await this.$dialog({
          text: '',
          cancelHighlight: true,
        });
        this.tableData.pop();
      } catch (_) {}
    }
  },
};
</script>
<style lang="scss">
$borderColor: #ccc;

.ld-table {
  margin-top: 2rem;
}
.ld-table th {
  font-weight: normal;
}
.ld-table input {
  text-align: right;
}

.ld-table {
  width: 100%;
  font-size: 1.6rem;
  border-collapse: collapse;
  table-layout: fixed;
}
.ld-table .th.row {
  background: #f7f7f7;
  line-height: 1.5em;
  text-align: center;
  border: solid $borderColor;
  border-width: 1px 0;
}
.ld-table .tr.row {
  height: 2em;
  line-height: 2em;
  text-align: center;
  border-bottom: 1px solid $borderColor;
}
.ld-table .td-border {
  border: 1px solid $borderColor;
}
.ld-table .row-order {
  width: 5rem;
  position: relative;
  &::before {
    content: 'x';
    font-size: 1.6rem;
    position: absolute;
    right: 0;
    top: 0;
    line-height: 1.6rem;
  }
}
.ld-table .td input {
  width: 100%;
}

.icon-plus {
  width: 5rem;
  height: 5rem;
  margin: 0 auto;
  background: url('../assets/img/add.png') no-repeat 50% 50% transparent;
  background-size: 1.6rem 1.6rem;
}
</style>



