<template>
  <div class="app-container">
    <div>
      <ld-header></ld-header>
    </div>
    <div class="view-container" v-if="config">
      <view-a
        :config="config"
        :onClick="click"
      ></view-a>
    </div>
    <div>
      <ld-bottom :list="bottom"></ld-bottom>
    </div>
  </div>
</template>

<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ld from '../../utils/ld.js';
import service from '../../service/index.js';
import { GET_BILL_TYPE } from '../../http/url';
import ViewA from '../../view/A';

export default {
  data() {
    const that = this;

    return {
      config: null,

      // 
      click: function (item, rowIdx) {
        const storageKey = `${that.config.jianzhiming}_${rowIdx}`;
        ld.recordChildKey(storageKey, rowIdx); // storageKey
        ld.navigateTo(that.config.identifyValue, {
          storageKey: storageKey,
        });
      },

      // 
      bottom: [
        {},
        {
          icon: 'confirm',
          onClick() {
            const urlParams = ld.getUrlParams();

            if (that.storageKey) {
              ld.globalData.set(that.storageKey, that.config.tableData);
            }

            ld.navigateBack();
          }
        }
      ]
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    /**
     * 
     */

    async loadData() {
      try {
        let urlParams;
        let subjectNo;
        let billTypeData; // 
        let fieldConfig; // 
        let viewConfig; // 
        let storageKey; // 
        let storageData; // 
        let tableData; // 

        // url
        urlParams = ld.getUrlParams();
        subjectNo = urlParams.subjectNo;
        storageKey = urlParams.storageKey;

        this.storageKey = storageKey;

        storageData = ld.globalData.get(storageKey);
        if (storageData) {
          tableData = storageData;
        } else {
          // 
          // billTypeData = await service.getDecodeData(GET_BILL_TYPE, {
          //   'SubjectNo': subjectNo
          // });
          billTypeData = await service.getMockData('/E12/index4', {
            'SubjectNo': subjectNo
          });
          // 1
          tableData = billTypeData.table;
        }

        // 
        fieldConfig = await service.getMockFieldConfig('/E12/index4');

        // 
        viewConfig = await service.getMockViewConfig('/E12/index4');

        // 
        this.config = {
          tableData: tableData, // 
          fieldConfig: fieldConfig, // 
          ...viewConfig,
        };

        // 
        this.handleChildData();

      } catch (err) {
        console.log(err);
      }
    },

    handleChildData() {
      let childKeyMap; // storage key
      let rowIdxs; // 

      childKeyMap = ld.getChildKeyMap();
      rowIdxs = Object.keys(childKeyMap);

      if (!rowIdxs.length) {
        return;
      }

      // rowIdx 
      rowIdxs.forEach(rowIdx => {
        // 
        const childKey = childKeyMap[rowIdx];
        const childData = ld.globalData.get(childKey);

        this.config.tableData[rowIdx].zhangshu = ld.sumWithKey(childData, 'zhangshu'); // 
        this.config.tableData[rowIdx].jine = ld.sumWithKey(childData, 'heji'); // 
      })
    }
  },
  components: {
    'ld-header': Header,
    'ld-bottom': Bottom,
    'view-a': ViewA,
  },
};
</script>

<style scoped>
.view-container {
  padding-left: 0;
  padding-right: 0;
}
</style>



