import Vue from 'vue';
import Router from 'vue-router';
import Y1 from './Y1';
import E12 from './E12';
import TestIndex1 from '../pages/test/index1';
import TestIndex2 from '../pages/test/index2';
import TestIndex3 from '../pages/test/index3';
import TestIndex4 from '../pages/test/index4';
import Render from '../pages/test/Render';

Vue.use(Router);

export default new Router({
  routes: [
    ...Y1,
    ...E12,
    {
      path: '/test/index1',
      component: TestIndex1,
    },
    {
      path: '/test/index2',
      component: TestIndex2,
    },
    {
      path: '/test/index2',
      component: TestIndex2,
    },
    {
      path: '/test/index3',
      component: TestIndex3,
    },
    {
      path: '/test/index4',
      component: TestIndex4,
    },
    {
      path: '/test/render',
      component: Render,
    }
  ],
});



