import cookie from '../utils/cookie';

const store = {
  serverIP: null,
  userMark: null,
};

export function getServerIP() {
  if (store.serverIP) {
    return store.serverIP;
  }

  const serverIP = cookie.get('ServerOutIP');
  store.serverIP = serverIP;
  return serverIP;
}
export function getUserMark() {
  if (store.userMark) {
    return store.userMark;
  }

  const userMark = cookie.get('UserMark');
  store.userMark = userMark;
  return userMark;
}



