<template>
  <div>
    <div>
      <ld-header></ld-header>
    </div>
    <div>
      <view-a :config="config" :tableData="tableData"></view-a>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';

const config = {
  viewId: 'view3', // 
  viewName: '', // 
  getId: '', // ID
  // fieldList: ['piaoju', 'zhangshu', 'jine'], // 
  // fieldTitle: ['', '', ''], // 
  // inputType: 1, //  0  1 
  fieldConfig: [
    {
      fieldName: 'piaoju',
      fieldTitle: '',
      inputType: 1,
    },
    {
      fieldName: 'zhangshu',
      fieldTitle: '',
      inputType: 1,
    },
    {
      fieldName: 'jine',
      fieldTitle: '',
      inputType: 1,
    },
  ],
  nextViewId: 'view4', // ID
};

export default {
  data() {
    return {
      config,
      tableData: [
        {
          piaoju: ''
        },
        {
          piaoju: ''
        },
        {
          piaoju: ''
        }
      ],
    };
  },
  components: {
    'view-a': ViewA,
    'ld-header': Header,
    'ld-bottom': Bottom,
  }
};
</script>



