import { get } from '../http/ajax';
import * as main from './mainService';
import '../vendor/base64.min';
import '../vendor/jsxcompressor.min';
import mockData from './mockData';
import { GET_QUERY_DATA } from '../http/url';

/**
 * 
 *
 * @param {string} resText 
 * @return {string} 
 */

function decompress(resText) {
  if (!~['null', 'NULL', '-1', -1].indexOf(resText) && resText) {
    // 
    return JXG.decompress(resText);
  }
  return null;
}

/**
 * 
 *
 * @param {string} dec 
 * @return {Object} 
 */

function resolveResult(dec) {
  const obj = JSON.parse(dec);
  const { FIELD: field, TABLE: table, TextFIELD: txtField } = obj;
  const fieldList = field.split('.');
  const txtFieldList = txtField.split('.'); // base64

  obj.table = table.map(item => {
    const o = {};
    fieldList.forEach((f, i) => {
      if (~txtFieldList.indexOf(f)) {
        o[f] = JXG.Util.Base64.decode(item[`F${i}`], true);
      } else {
        o[f] = item[`F${i}`];
      }
    });
    return o;
  });

  return obj;
}

/**
 * 
 *
 * @param {string} path 
 * @param {Object} data 
 * @return {Promise<string>} 
 */

export function getData(path, data) {
  const ServerIP = main.getServerIP();
  const UserMark = main.getUserMark(); // 

  const url = `http://${ServerIP}/WebOA.dll${path}`;

  // for test
  if (~url.indexOf('TCA004_Subject')) {
    return '';
  }

  return get(url, {
    UserMark,
    ...data,
  });
}

/**
 * 
 *
 * @param {*} path 
 * @param {*} data 
 * @param {Promise<any>} 
 */

export async function getDecodeData(path, data) {
  const res = await getData(path, data);
  const dec = decompress(res);

  if (dec) {
    return resolveResult(dec);
  }

  return dec;
}

/**
 * 
 *
 * @param {string} tableName 
 * @param {string} fields 
 * @param {string} where where
 * @return {Promise<any>} 
 */

export async function getQueryData(tableName, fields, where) {
  const res = await getDecodeData(GET_QUERY_DATA, {
    Sourse: tableName,
    KeyWord: fields,
    WhereStr: where,
  });
  return res;
}

/**
 * 
 *
 * @param {string} id id
 */

export function getMockData(id) {
  const data = mockData.get(id);
  if (!data) {
    return null;
  }

  return JSON.parse(JSON.stringify(data));
}

/**
 * 
 *
 * @param {string} id id
 */

export function getMockAddData(id) {
  const data = mockData.getAddData(id);
  if (!data) {
    return null;
  }

  return JSON.parse(JSON.stringify(data));
}

/**
 * 
 *
 * @param {string|number} id 
 */

export function getMockFieldConfig(id) {
  const data = mockData.getFieldConfig(id);
  if (!data) {
    return null;
  }

  return JSON.parse(JSON.stringify(data));
}

export function getMockViewConfig(id) {
  const data = mockData.getViewConfig(id);
  if (!data) {
    return null;
  }

  return JSON.parse(JSON.stringify(data));
}



