<template>
  <div>
    <div class="app-list">
      <div
        class="app-item ld-border bottom"
        v-for="item in appList"
        :key="item.text"
        @touchend="onToApp(item.name)"
      >
        <div
          class="app-icon"
          :style="`background-image: url('/static/Y1/icon_${item.name}.png');`"
        />
        <div class="app-text">{{ item.text }}</div>
      </div>
    </div>
    <ld-bottom :list="bottom"></ld-bottom>
  </div>
</template>
<script>
import Bottom from '@/components/Bottom';
import service from '../../service/index.js';
import { HOME_GET_INV } from '../../http/url';
import ld from '../../utils/ld';
import {
  isWeixin, loadWxJS,
} from '../../utils/wechat';
import cookie from '../../utils/cookie';

// 
const appMap = {
  E2: {
    text: '',
  },
  E12: {
    text: '',
  },
  Y12: {
    text: ''
  },
  Y5_1: {
    text: ''
  },
  Y5_2: {
    text: ''
  },
  Y8: {
    text: ''
  },
  Y9: {
    text: ''
  },
  Y2: {
    text: ''
  },
  Y6: {
    text: ''
  },
  Y18: {
    text: ''
  },
};

export default {
  components: {
    'ld-bottom': Bottom,
  },
  data() {
    return {
      appList: [],
      bottom: [
        {},
        {
          icon: 'add',
        }
      ],
      serverIP: null,
      userMark: null,
    };
  },
  mounted() {
    ld.initTitle();

    if (isWeixin()) {
      loadWxJS();
    }

    this.loadData();

    this.serverIP = cookie.get('ServerOutIP');
    this.userMark = cookie.get('UserMark');
  },
  methods: {
    // app
    onToApp(name) {
      const nameArr = name.split('_');
      const appBlKindNo = nameArr[0];
      let url = `/WebOA/${appBlKindNo}/HTML/?ServerIP=http://${this.serverIP}&loginid=${this.userMark}&EnterType=0`;

      if (nameArr.length > 1) {
        url += `&BlKindNo=${nameArr[1]}`;
      }

      location.href = url;
    },
    async loadData() {
      try {
        // appList

        const str = await service.getData(HOME_GET_INV);
        let arr = null;

        if (!str) {
          arr = Object.keys(appMap);
        } else {
          arr = str.split(',');
        }

        this.appList = arr.map(k => ({
          name: k,
          ...appMap[k],
        }));
      } catch (err) {
        console.log(err);
      }
    },
  }
};
</script>
<style>
.app-list {
  padding: 0 0.8rem;
}
.app-item {
  display: flex;
  align-items: center;
  padding: 1.05rem 0;
}
.app-icon {
  width: 3.5rem;
  height: 3.5rem;
  background-size: 3.5rem 3.5rem;
  margin-right: 1.1rem;
}
</style>



