<template>
  <div>
    <div>
      <ld-header></ld-header>
    </div>
    <div>
      <view-a :config="config" :tableData="tableData"></view-a>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';

export default {
  data() {
    return {
      config: {
        viewId: 'view3', // 
        viewName: '', // 
        getId: '', // ID
        // fieldList: ['piaoju', 'zhangshu', 'jine'], // 
        // fieldTitle: ['', '', ''], // 
        // inputType: 1, //  0  1 
        fieldConfig: [
          {
            fieldName: 'zhangshu',
            fieldTitle: '',
            inputType: 3,
          },
          {
            fieldName: 'danjia',
            fieldTitle: '',
            inputType: 4,
          },
          {
            fieldName: 'heji',
            fieldTitle: '',
            inputType: 0,
          },
          {
            fieldName: 'beizhu',
            fieldTitle: '',
            inputType: 2,
          },
        ],
        nextViewId: 'view4', // ID

        // 
        canEdit: true,
      },
      tableData: [{}, {}, {}],
    }
  },
  components: {
    'view-a': ViewA,
    'ld-header': Header,
    'ld-bottom': Bottom,
  }
}
</script>


