<template>
  <div class="list">
    <div
      class="theme0-container"
      v-if="theme === 0">
      <div
        class="theme0-item"
        v-for="(item, index) in config.tableData"
        :key="index">
        {{ item[config[fieldConfig[0].fieldName]] }}
      </div>
    </div>
    <template v-else>
      <div
        class="bx-item ld-border bottom"
        v-for="(item, index) in config.tableData"
        :key="index"
        @touchend="onClick(item, index)">
        {{ item.name }}
      </div>
    </template>
  </div>
</template>
<script>
/**
 *  config
 * config.tableDatatableDataname
 */
import ld from '../utils/ld';

export default {
  props: ['config', 'theme'],
  methods: {

    /**
     * @param {Object} item 
     */

    onClick(item) {
      ld.navigateTo(this.config, item);
    }
  },
}
</script>
<style>
.bx-item {
  padding: 0 0.8rem;
  line-height: 4.8rem;
  display: flex;
  justify-content: space-between;
}
.theme0-container {
  flex: 1;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  overflow: auto;
}
.theme0-item {
  width: 11.4rem;
  height: 5rem;
  margin-bottom: 1rem;
  line-height: 5rem;
  text-align: center;
  border-radius: 5px;

  background: rgb(224, 233, 240);
  border: 1px solid rgb(210, 210, 210);

  padding: 0 0.5rem;
}
</style>


