import router from '../router';
import { VIEW_TYPE_A, VIEW_TYPE_B, CHILD_DATA_VALUE_KEY, CHILD_DATA_INDEX_KEY, CHILD_DATA_KEY_MAP } from './const';

/**
 * url
 *
 * @param {string} urlParams url
 */

function urlParamsToObject(urlParams) {
  const obj = {};
  const arr = urlParams.split('&');

  arr.forEach(str => {
    const item = str.split('=');
    obj[item[0]] = item[1];
  });

  return obj;
}

/**
 * 
 */
const globalData = {
  /**
   * 
   *
   * @param {string} k 
   * @param {string | Object} v 
   */

  set(k, v) {
    try {
      if (typeof v === 'object') {
        v = JSON.stringify(v);
      }
    } catch (err) {}
    sessionStorage.setItem(k, v);
  },

  /**
   * 
   *
   * @param {string} k 
   * @return {string | Object} 
   */

  get(k) {
    let v = sessionStorage.getItem(k);
    try {
      v = JSON.parse(v);
    } catch (err) {}
    return v;
  },

  /**
   * 
   */

  getAll() {
    const result = {};
    Object.keys(sessionStorage).forEach(key => {
      result[key] = this.get(key);
    });
    return result;
  }
};

export default {

  /**
   * storage
   */

  getChildKeyMap() {
    return globalData.get(CHILD_DATA_KEY_MAP) || {};
  },

  /**
   * storagehash
   * {
   *   1: storage1
   *   2: storage2
   * }
   *
   * @param {string} key storage
   * @param {number} rowIdx 
   */

  recordChildKey(key, rowIdx) {
    let keyMap;
    keyMap = globalData.get(CHILD_DATA_KEY_MAP);

    // 
    if (!keyMap || typeof keyMap !== 'object') {
      keyMap = {};
      keyMap[rowIdx] = key;
      globalData.set(CHILD_DATA_KEY_MAP, keyMap);
      return;
    }

    // 
    if (!keyMap[rowIdx]) {
      keyMap[rowIdx] = key;
      globalData.set(CHILD_DATA_KEY_MAP, keyMap);
      return;
    }
  },

  /**
   * 
   */

  clearChilrenData() {
    globalData.set(CHILD_DATA_VALUE_KEY, '');
    globalData.set(CHILD_DATA_INDEX_KEY, '');
  },

  /**
   * 
   *
   * @param {Array<Object>} arr 
   * @param {*} key 
   * @return {number} 
   */
  sumWithKey(arr, key) {
    if (!Array.isArray(arr)) {
      return 0;
    }

    return arr.reduce((pre, cur) => {
      return pre + (Number(cur[key]) || 0);
    }, 0);
  },

  // 
  globalData,

  /**
   * id
   *
   * @param {Object} config 
   * @param {string} config.nextViewId id
   * @param {Array} config.nextPageQuery 
   * @param {Object} item 
   * @param {number} rowIdx 0
   */
  navigateTo(config, item = {}, rowIdx) {
    if (typeof config === 'string') {
      // 
      router.push({ path: config, query: item || {} });
    }
    // AidentifyValueBnextViewId
    let viewId = null;
    if (config.viewType === VIEW_TYPE_A) {
      viewId = config.identifyValue;
    } else if (config.viewType === VIEW_TYPE_B) {
      viewId = config.nextViewId;
    } else {
      return;
    }

    const extraQuery = {}; // 

    if (config.nextPageQuery) {
      const state = config.state;
      // url
      const bigData = {
        ...state,
        ...item,
      };

      const nextPageQuery = config.nextPageQuery; // 
      const obj = urlParamsToObject(nextPageQuery); // url

      Object.keys(obj).forEach(k => {
        const stateParam = obj[k]; // 
        extraQuery[k] = bigData[stateParam]; // 
      });
    }

    router.push({ query: { viewId: viewId, ...extraQuery, keyId: `${config.jianzhiming}_${rowIdx}` }});

    // 
    this.clearChilrenData();
  },

  /**
   * 
   */
  navigateBack() {
    router.go(-1);
  },

  /**
   * url
   *
   * @return {Object} url
   */
  getUrlParams() {
    return router.currentRoute.query;
  },

  /**
   * 
   * @param {string} title 
   */
  initTitle(subTitle) {
    const prefix = 'OA';
    document.title = prefix + (subTitle ? `\\${subTitle}` : '');
  },

  /**
   * 
   *
   * @param {number} time 
   */
  sleep(time = 200) {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve();
      }, time);
    });
  }
};



