<template>
  <div>
    <ld-header></ld-header>
    <div class="bx-list">
      <div
        class="bx-item ld-border bottom"
        v-for="(item, index) in list"
        :key=index
        @touchend="onClick(item)">
        <template v-if="item.BlNum !== 'null'">
          <div class="name">{{ `${item.TypeName}(${item.BlNum})` }}</div>
          <div class="value">{{ Number(item.ExpenseSum).toFixed(2) }}</div>
        </template>
      </div>
    </div>
    <ld-bottom :list="bottom"></ld-bottom>
  </div>
</template>
<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import service from '../../service/index.js';
import ld from '../../utils/ld';
import { BL_LIST } from '../../http/url';

export default {
  data() {
    return {
      bottom: [
        {},
        {
          icon: 'add',
          onClick() {
            const url =
              `/WebOA/E12/HTML/index10.html?ServerIP=http://${this.serverIP}&loginid=${this.userMark}&EnterType=${this.enterType};`;
            location.href = url;
          }
        }
      ],
      serverIP: '',
      userMark: '',
      list: [],
    };
  },
  components: {
    'ld-header': Header,
    'ld-bottom': Bottom,
  },
  mounted() {
    try {
      ld.initTitle('');
      this.serverIP = service.getServerIP();
      this.userMark = service.getUserMark();
      this.enterType = ld.getUrlParams().EnterType || '';

      // 
      // if ((document.referrer).indexOf('/Y1/') >= 0) {
      //   sessionStorage.setItem('E12_history', '../../Y1/HTML/main.html');
      // } else if ((document.referrer).indexOf('/E1/') >= 0) {
      //   sessionStorage.setItem('E12_history', '../../E1/HTML/');
      // }

      this.loadData(); // 
    } catch (err) {
      console.log(err);
    }
  },

  methods: {
    async loadData() {
      const res = await service.getDecodeData(BL_LIST);
      if (res) {
        this.list = res.table;
      }
    },
    onClick(item) {
      const url =
        `/WebOA/E12/HTML/index2.html?ServerIP=http://${this.serverIP}&loginid=${this.userMark}&EnterType=${this.enterType}`;

      const { TypeNo, TypeName } = item;
      sessionStorage.setItem('TypeNo', TypeNo);
      sessionStorage.setItem('Nm', TypeName);
      location.href = url;
    }
  }
};

</script>
<style>
.bx-item {
  padding: 0 0.8rem;
  line-height: 4.8rem;
  display: flex;
  justify-content: space-between;
}
</style>



